export default [
  {
    path: '/home/area',
    name: '地区选择',
    component: resolve => require(['@/components/selectArea'], resolve)
  },
  {
    path: '/home/login',
    name: '登录',
    component: resolve => require(['@/views/login/login'], resolve)
  },
  {
    path: '/home/activity/newcomer',
    name: '新人礼包',
    component: resolve => require(['@/views/activity/newComer'], resolve)
  },
  {
    path: '/home/activity',
    name: '活动列表',
    component: resolve => require(['@/views/activity/activityList'], resolve)
  }
]



